	module.exports = {"data":{"markdownRemark":{"html":"<p>“danmaq” は、<a href=\"http://www16.big.or.jp/%7Ezun/html/game.html\">東方Project</a>がメインの、たまに<a href=\"http://www.vocaloid.com/\">VOCALOID</a>やオリジナル作品なんかも作ったりする同人サークル “danmaq” の公式 Web ページです。Web ページの雰囲気があまり東方っぽくないように見えるかも知れませんが、気にしてはいけません。 サークル名 “danmaq” の由来は2004年頃“<a href=\"http://www.geocities.co.jp/SiliconValley-Oakland/9951/\">東方弾幕風</a>”のステージスクリプトの人で知られていたので “弾幕” に “<a href=\"http://www.ongaq.com/\">ongaq</a>”に対するリスペクトを掛け合わせたものです。  ドイツ語圏では『ク』を『q』と表記するそうで。特にdanmaqってスペル自体には意味はありません。アゼルバイジャン語で何らかの意味があるのかも。 サークル代表は “Mc(まく)” と言います。<a href=\"/tag/head\">基本ソロサークルなのでメンバーは私一人</a>です。原則として、プロジェクトごとにメンバーを募って、プロジェクト完了後解散する形をとっていますが、腐れ縁で何度もおんなじメンバーになったりとかもにょもにょ</p>","frontmatter":{"cover":null,"date":"2012-11-27T23:59:26+09:00","strDate":"2012/11/27","title":"danmaqとは"}}},"pathContext":{"path":"/ja/blog/2012/11/27-danmaq/","langKey":"ja"}}

